import React from 'react';
import Component from 'react-pure-render/component';
import InnerPage from 'routes/InnerPage';
import Feedback from 'components/Feedback';
import text from 'data/text.yml';
import links from 'data/links.yml';
import linkify from 'components/linkify';

const lang = process.env.LANG;

export default class XsollaPage extends Component {

  render() {
    return (
      <InnerPage>
        <div className={styles.root}>
          <div className={styles.content}>
            <div className={styles.text1}>{linkify(text.pages.header[lang])}</div>
            <h2 className={styles.text2}>{linkify(text.pages.xsolla.text1[lang])}</h2>
            <div className={styles.cols}>
              <div className={styles.text3}>
                <p>{linkify(text.pages.xsolla.text2[lang])}</p>
                <p>
                  <a href={links.xsolla}
                     target='_blank'>{linkify(text.pages.xsolla.company[lang])}</a> {linkify(text.pages.xsolla.text3[lang])}
                </p>
                <p className={styles.text4}>{linkify(text.pages.xsolla.text4[lang])}</p>
              </div>
              <div className={styles.divX}>
                <div className={styles.img1}></div>
              </div>
            </div>
          </div>
        </div>
        <div className={styles.img2}></div>
        <div className={styles.root}>
          <div className={styles.content2}>
            <div className={styles.text5}>
              <p>{linkify(text.pages.xsolla.text5[lang])}</p>
            </div>
            <div className={styles.img3}></div>
            <div className={styles.img4}></div>
            <div className={styles.img5}></div>
            <div className={styles.img6}></div>
            <div className={styles.img7}></div>
          </div>
        </div>
        <div className={styles.border}></div>
        <Feedback/>
      </InnerPage>
    );
  }

}



